/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  background: #000000;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 1.6666666667rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

button {
  cursor: pointer;
  background: none;
  border: none;
}

li {
  list-style: none;
  text-align: center;
  display: inline;
}

article {
  display: none;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

/* UI */
.a, .b {
  background: linear-gradient(90deg, rgb(44, 120, 115) 0%, rgb(111, 185, 143) 50%, rgb(44, 120, 115) 100%);
}

.a {
  position: absolute;
  width: 20%;
  height: 30%;
  top: 0;
}

.b {
  position: absolute;
  width: 20%;
  height: 30%;
  right: 0;
  bottom: 0;
}

.container-page {
  z-index: 1;
  position: absolute;
  top: 2%;
  left: 1%;
  width: 97.5%;
  height: 95%;
  background: #293d3d;
  border-radius: 10px;
  border: 1px #2C7873 solid;
  display: grid;
  grid-template-columns: 1fr 9fr;
}

.container-pages::-webkit-scrollbar {
  width: 10px;
}

.container-pages {
  padding-bottom: 1rem;
  overflow-y: auto;
  border-radius: 10px;
}

@media (max-width: 1280px) {
  .container-page {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 950px) {
  .a {
    position: absolute;
    width: 30%;
    height: 40%;
  }
  .container-page {
    z-index: 1;
    position: absolute;
    top: 2%;
    left: 2%;
    width: 95%;
    height: 95%;
  }
  .b {
    position: absolute;
    width: 30%;
    height: 40%;
  }
}
.logo {
  width: 90%;
  height: 30%;
  display: flex;
  align-items: center;
  background: #021C1E;
}

.menu {
  z-index: 1;
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
  background: #021C1E;
}

.menu-phone {
  display: none;
}

.container-navbar {
  display: flex;
}

.navbar-item {
  width: 100%;
}

.navbar-list {
  padding: 0;
}

.navbar-link {
  width: 100%;
  padding: 20px 20px;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  color: #d6d6d6;
}

.active {
  display: block;
  background: #293d3d;
}

.navbar-link.active {
  color: #6FB98F;
}

.social-list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  padding-bottom: 4px;
  padding-left: 7px;
  justify-content: center;
}

.social-link {
  color: #6FB98F;
}

.social-link:hover {
  color: #d6d6d6;
}

@media (max-width: 1280px) {
  img {
    width: 50%;
  }
  .imagen-nombre {
    display: none;
  }
  .social-list {
    display: none;
  }
  .logo {
    display: none;
  }
  /* .menu{
      display: none;
  } */
  .menu {
    display: block;
    width: 100%;
    height: 70px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background: #021C1E;
  }
  .navbar-item {
    height: 100%;
    width: 100px;
  }
  .navbar-list {
    display: flex;
  }
  .navbar-link {
    width: 100%;
    padding: 26px 7px;
    font-family: "Poppins", sans-serif;
    font-weight: 300;
    color: #d6d6d6;
  }
  .navbar-link.active {
    color: #6FB98F;
  }
  .container-navbar {
    align-items: center;
  }
}
@media (max-width: 481px) {
  .menu {
    justify-content: center;
    height: 49px;
  }
  .navbar-link {
    width: 100%;
    padding: 17px 15px;
    font-family: "Poppins", sans-serif;
    font-weight: 300;
    color: #d6d6d6;
    font-size: 1.3rem;
  }
}
@media (max-width: 423px) {
  .navbar-list {
    align-items: center;
  }
  .navbar-item {
    width: 100%;
    height: 99%;
  }
  .menu {
    width: 100%;
  }
}
.subtitle {
  color: #d6d6d6;
  font-size: 3.5rem;
  font-weight: 400;
  font-family: "Bebas Neue", sans-serif;
}

.barra {
  content: "";
  position: relative;
  width: 90px;
  height: 7px;
  background: linear-gradient(90deg, rgb(44, 120, 115) 0%, rgb(111, 185, 143) 50%, rgb(44, 120, 115) 100%);
  border-radius: 3px;
}

.informacion {
  display: grid;
  align-items: center;
  justify-items: start;
}

.inicio {
  width: 100%;
  height: 100%;
}

.page-inicio {
  width: 100%;
  height: 100%;
  display: grid;
  align-content: center;
}

.title {
  color: #d6d6d6;
  font-weight: 700;
  font-family: "Bebas Neue", sans-serif;
  font-size: 8rem;
  margin: 0;
  padding: 0;
  line-height: 100px;
}

.titulo {
  color: #627978;
}

@media (max-width: 1280px) {
  .page-inicio {
    justify-content: center;
    height: 75%;
  }
  .title {
    font-size: 7rem;
    line-height: 100px;
  }
}
@media (max-width: 950px) {
  .title {
    font-size: 6rem;
  }
}
@media (max-width: 769px) {
  .title {
    font-size: 5rem;
    line-height: 100px;
  }
}
@media (max-width: 657px) {
  .title {
    font-size: 4rem;
    line-height: 80px;
  }
}
@media (max-width: 481px) {
  .title {
    font-size: 3rem;
    line-height: 50px;
  }
  .inicio {
    width: 100%;
  }
}
.text {
  font-weight: 300;
  color: #d6d6d6;
  font-size: 1.8rem;
}

/* .group-card{
    display: flex;
    gap: 20px;
    margin: 0;
