} */
.group-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  width: 90%;
  margin: 0;
}

.card-skill {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  width: 90%;
  padding: 20px 20px;
  box-shadow: 0 16px 30px hsla(0, 0%, 0%, 0.25);
  border-radius: 20px;
}

.img-card {
  display: flex;
  align-items: center;
  width: 45%;
}

.title-card {
  color: #d6d6d6;
  font-size: 2rem;
}

.text-card {
  color: #d6d6d6;
  font-size: 1.4rem;
}

.logo-empresa {
  width: 20rem !important;
}

@media (max-width: 1280px) {
  .text {
    text-align: left;
  }
  .informacion {
    padding: 20px;
    align-items: center;
  }
  .text {
    text-align: center;
  }
  .card-skill {
    width: 95%;
  }
  .img-card {
    width: 100%;
    justify-content: center;
  }
}
@media (max-width: 950px) {
  .subtitle {
    font-size: 3rem;
  }
}
@media (max-width: 769px) {
  .group-card {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 481px) {
  .sobre-mi {
    width: 100%;
  }
  .card-skill {
    grid-template-columns: 1fr;
    justify-items: start;
    width: 100%;
  }
}
a {
  text-decoration: none;
}

.divisiones {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
}

.icon-box {
  color: #6FB98F;
}

.timeline-wrapper {
  display: grid;
  gap: 1rem;
  margin-left: 1.5rem;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: center;
  width: 30%;
}

.timeline-wrapper2 {
  display: grid;
  align-items: center;
  justify-items: center;
  width: 50%;
}

.timeline-list {
  font-size: 15px;
}

.timeline-list span {
  color: #6FB98F;
  font-weight: 400;
  line-height: 1.6;
}

.timeline-list h4 {
  font-size: 1.7rem;
}

.timeline-list p {
  font-size: 1.2rem;
  color: #d6d6d6;
}

.timeline-list .text-xp {
  font-size: 1.2rem;
  color: #d6d6d6 !important;
}

.timeline-list .botton-xp {
  background-color: #2C7873;
  padding: 10px;
  border-radius: 5px;
  transition: transform 0.5s ease;
}

.timeline-list .botton-xp:hover {
  transform: scale(0.8);
  transition: transform 0.5s ease;
}

.timeline-item {
  display: grid;
  justify-items: start;
  position: relative;
  color: #d6d6d6;
  text-align: left;
}

.timeline-item:not(:last-child)::before {
  content: "";
  position: absolute;
  display: block;
  top: -25px;
  left: -15px;
  width: 2px;
  height: calc(100% + 40px);
  background: #4e5555;
}

.timeline-item::after {
  content: "";
  position: absolute;
  top: 5px;
  left: -18px;
  height: 9px;
  width: 9px;
  background: #2C7873;
  border-radius: 50%;
  box-shadow: 0 16px 30px hsla(0, 0%, 0%, 0.25);
}

.text-decoration {
  display: list-item;
  text-align: left;
  list-style: disc;
  color: #6FB98F;
}

.skills-icon {
  display: grid;
  grid-template-columns: 1fr;
}

.skills-icon h2 {
  text-align: left;
  color: #d6d6d6;
  font-size: 1.5rem;
}

.container-languages {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(3, 1fr);
}

.languages {
  color: #6FB98F;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.languages .text-xp {
  color: #d6d6d6;
  font-size: 1.5rem;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Estilos para el contenido de la modal */
.modal-content {
  background: #004445;
  padding: 20px;
  border-radius: 10px;
  width: 60%;
  text-align: center;
  position: relative;
  border: 1px #2C7873 solid;
}

/* Botón para cerrar */
.close {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 20px;
  cursor: pointer;
}

.container-visual {
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr 1fr;
}

.visualizador p {
  font-size: 1.5rem;
}

.container-visual p {
  font-size: 1.5rem;
}

.gif-visualizador {
  width: 100%;
}

.sep {
  margin-bottom: 1rem;
}

@media (max-width: 950px) {
  /* Estilos para el contenido de la modal */
  .modal-content {
    width: 95%;
    height: 85%;
    overflow-x: scroll;
  }
  .gif-visualizador {
    width: 100%;
  }
  .container-visual {
    display: grid;
    grid-template-columns: 1fr;
  }
}
@media (max-width: 769px) {
  .divisiones {
    grid-template-columns: 1fr;
  }
  .skills-icon {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}
@media (max-width: 481px) {
  .resumen {
    width: 100%;
  }
  .skills-icon {
    grid-template-columns: 1fr;
  }
}
.container-work {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.work-item {
  width: 70% !important;
}

.container-item__text span {
  color: #6FB98F;
}

.container-item__text p {
  color: #d6d6d6;
  font-size: 20px;
}

.work-item:hover {
  filter: opacity(20%);
  transform: scale(0.98);
  transition: transform 0.3s ease;
  transition: filter 0.3s ease;
}

@media (max-width: 769px) {
  .container-work {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 481px) {
  .portafolio {
    width: 100%;
  }
  .container-work {
    grid-template-columns: 1fr;
  }
}