.a, .b{
    background: $fondo_gradiante;
}

.a{
    position: absolute;
    width: 20%;
    height: 30%;
    top: 0;
}

.b{
    position: absolute;
    width: 20%;
    height: 30%;
    right: 0;
    bottom: 0;
}

.container-page{
    z-index: 1;
    position: absolute;
    top: 2%;
    left: 1%;
    width: 97.5%;
    height: 95%;
    background: $fondo;
    border-radius: 10px;
    border: 1px $rain solid;
    display: grid;
    grid-template-columns: 1fr 9fr;
} 

.container-pages::-webkit-scrollbar {
    width: 10px;
}

.container-pages{
    padding-bottom: 1rem;
    overflow-y: auto;
    border-radius: 10px;
}

@media (max-width: 1280px){ 
    .container-page{
        grid-template-columns: 1fr;
    }
}


@media (max-width: 950px) { 
    .a{
        position: absolute;
        width: 30%;
        height: 40%;
    }
    
    .container-page{
        z-index: 1;
        position: absolute;
        top: 2%;
        left: 2%;
        width: 95%;
        height: 95%;
    }
    
    .b{
        position: absolute;
        width: 30%;
        height: 40%;
    }
}